import "./styles.css";
import { SnackbarProvider, enqueueSnackbar, closeSnackbar } from "notistack";

import {
  InferenceContext,
  InferenceResult,
  PhotoCollector,
} from "landingai-react";
import { useState } from "react";

/**
 * After cloning this sandbox, please follow https://codesandbox.io/docs/learn/environment/secrets#from-the-ui
 * to add your environment variables REACT_APP_API_KEY and REACT_APP_API_SECRET.
 */
const API_INFO = {
  endpoint:
    "https://predict.app.landing.ai/inference/v1/predict?endpoint_id=06d64c2e-7aa6-4925-91b2-1c732fa23a65",
  key: 'land_sk_uzKP4G8YyZwfzeBogv6ucgriJIoHlmyyvoMgJlpKLAcYn6kAsq',
};

export default function App() {
  const [image, setImage] = useState();
  const [showLabels, setLabels] = useState(false);
  return (
    <InferenceContext.Provider value={API_INFO}>
      <SnackbarProvider
        action={(key) => (
          <div
            onClick={() => closeSnackbar(key)}
            style={{ padding: "2px 10px" }}
          >
            
          </div>
        )}
      />
      <div className="App">
        <div style={{ textAlign: "center", marginBottom: 12 }}>
          <img
            src="https://github.com/landing-ai/landingai-python/raw/main/assets/avi-logo.png"
            width={30}
          />
        </div>
        <PhotoCollector setImage={setImage} />
        <label style={{ display: "block", paddingTop: 10 }}>
          Show labels:{" "}
          <input
            type="checkbox"
            checked={showLabels}
            onChange={(e) => setLabels((prev) => !prev)}
          />
        </label>
        <InferenceResult
          image={image}
          onPredictError={(err) => {
            console.error(err);
            enqueueSnackbar(err.body?.message ?? err.message, {
              variant: "error",
              autoHideDuration: 12000,
              preventDuplicate: true,
            });
          }}
          showLabels={showLabels}
        />
      </div>
    </InferenceContext.Provider>
  );
}
